    generateMatrix () {
      this.matrixcombination = [];
      let iterables = [];
      for(let i = 0; i<this.value10.length;i++ ){
        iterables.push(this.value10[i].list)
      }
      let tmp = _.reduce(iterables, function(a, b) {
            return _.flatten(_.map(a, function(x) {
                return _.map(b, function(y) {
                    return x.concat([y]);
                });
            }), true);
        }, [ [] ]);
      for(let j = 0; j<tmp.length; j++){
        let obj = {
          id:j,
          name: (name='')=>{
            getName : {
              for (let k=0; k<tmp[j].length; k++){
                  name+=tmp[j][k].label
              }
              return name;
            }
          },
          values: tmp[j]
        }
        this.matrixcombination.push(obj)
        if(j == tmp.length-1){
            console.log('matrix permutation done..');
            if(this.matrix_status){
              this.openSuccess('Variation Matrix loaded')
            }
        }
      }
      
    }
